import React from "react";
import { useForm } from "react-hook-form";

const USERNAME_REGEX = /^[A-z][A-z0-9-_]{3,23}$/;
const PWD_REGEX = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%]).{8,24}$/;

function Register() {
  const {
    register,
    handleSubmit,
    formState: { errors },
    setValue,
  } = useForm();

  const onSubmit = React.useCallback((data) => {
    console.log(data);
  }, []);

  const handleInputChange = React.useCallback(
    (event) => {
      const { name, value } = event.target;
      setValue(name, value, true);
    },
    [setValue]
  );

  const isFormValid =
    !!errors.pseudo && !!errors.email && !!errors.pwd && !!errors.confirm_pwd;

  return (
    <section
      className={`register flexCenter`}
    >
      <div>
        <h1>Register</h1>
        <form onSubmit={handleSubmit(onSubmit)} className="flexCenter">
          <input
            name="pseudo"
            type="text"
            {...register("pseudo", {
              required: "Champ requis !",
              pattern: {
                value: USERNAME_REGEX,
                message: "Pseudo invalide !",
              },
            })}
            placeholder="Votre pseudo*"
            onChange={handleInputChange}
            aria-invalid={errors.pseudo ? "true" : "false"}
          />
          {errors.pseudo && <p role="alert">{errors.pseudo.message}</p>}

          <input
            name="email"
            type="email"
            {...register("email", {
              required: "Champ requis !",
              pattern: {
                value: /^\S+@\S+$/i,
                message: "Email invalide !",
              },
            })}
            placeholder="Votre email*"
            className="contactForm-item"
            onChange={handleInputChange}
            aria-invalid={errors.email ? "true" : "false"}
          />
          {errors.email && <p role="alert">{errors.email.message}</p>}

          <input
            name="pwd"
            type="password"
            {...register("pwd", {
              required: "Champ requis !",
              pattern: {
                value: PWD_REGEX,
                message: "Mot de passe invalide !",
              },
            })}
            placeholder="Votre mot de passe*"
            onChange={handleInputChange}
            aria-invalid={errors.pwd ? "true" : "false"}
          />
          {errors.pwd && <p role="alert">{errors.pwd.message}</p>}

          <input
            name="confirm_pwd"
            type="password"
            {...register("confirm_pwd", {
              required: "Champ requis !",
              validate: (value) =>
                value === register("pwd").value ||
                "Les mots de passe ne correspondent pas !",
            })}
            placeholder="Confirmez votre mot de passe*"
            onChange={handleInputChange}
            aria-invalid={errors.confirm_pwd ? "true" : "false"}
          />
          {errors.confirm_pwd && (
            <p role="alert">{errors.confirm_pwd.message}</p>
          )}

          <input
            value="register"
            type="submit"
            disabled={isFormValid}
            // transform, animation={isFormValid ? "none" : ""}
          />
        </form>
      </div>
    </section>
  );
}

export default Register;
